.wrapper {
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-gap: 10px;
  grid-template-areas:
    "header header"
    "nav main";
}

@media screen and (max-device-width: 767px) {
  .wrapper {
    grid-template-rows: 60px 30px 1fr;
    grid-gap: 5px;
    grid-template-areas:
      "header header"
      "nav nav"
      "main main";
  }
}

